import {  ListItemText } from '@material-ui/core'
import React from 'react'
import {List,ListItem,ListItemAvatar} from '@material-ui/core'
import './ToDo.css'
function ToDo(props) {
    return (
        <>
        <List className="todo_list">
            <ListItem>
                <ListItemAvatar>
                  
                </ListItemAvatar>
           <ListItemText primary="Todo" secondary={props.text} />
            </ListItem>
        </List>
        
            {/* <li>{props.text}</li> */}
            </>
        
    )
}

export default ToDo
